:root {
  /* Cores principais da Double Diamond */
  --diamond-purple: #8447FF;
  --diamond-purple-dark: #4B00E0;
  --diamond-purple-light: #BB99FF;
  --diamond-pink: #D972FF;
  --diamond-orange: #EF695B;
  --diamond-pink-dark: #A500E0;
  --diamond-pink-light: #E9ADFF;
  --diamond-orange-dark: #FF3633;
  --diamond-orange-light: #FFC3C2;
  --dark-grey: #2E3632;
  --white: #FFFFFF;
  --grey-4: #55625D;
  --grey-3: #55625D;
  --grey-2: #B3BDB9;
  --grey-1: #F4F6F5;
  
  /* Cores Thiago Pessoa */
  --tp-gold: #D4AF37;
  --tp-gold-light: #F5E7A3;
  --tp-black: #000000;
  --tp-silver: #C0C0C0;
  
  /* Variáveis de uso geral */
  --background: #0A0B1A;
  --text: #FFFFFF;
  --text-secondary: rgba(255, 255, 255, 0.7);
  --card-bg: rgba(255, 255, 255, 0.05);
  --card-border: rgba(255, 255, 255, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--background);
  color: var(--text);
  line-height: 1.6;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  margin-bottom: 1rem;
}

h1 {
  font-size: 3.5rem;
  line-height: 1.2;
}

h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

h3 {
  font-size: 1.8rem;
}

p {
  margin-bottom: 1rem;
}

a {
  color: var(--text);
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: var(--diamond-purple);
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

section {
  padding: 5rem 0;
}

.btn {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, var(--diamond-purple), var(--diamond-pink));
  color: var(--white);
  border: none;
  border-radius: 30px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  text-align: center;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  color: var(--white);
}

.btn-secondary {
  background: transparent;
  border: 2px solid var(--diamond-purple);
  color: var(--text);
}

.btn-secondary:hover {
  background: rgba(132, 71, 255, 0.1);
}

.card {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  padding: 2rem;
  margin-bottom: 2rem;
  backdrop-filter: blur(10px);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.section-title {
  text-align: center;
  margin-bottom: 1rem;
}

.section-subtitle {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 3rem;
  color: var(--text-secondary);
}

.gradient-text {
  background: linear-gradient(to right, var(--diamond-purple), var(--tp-gold));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Animações de fundo */
.bg-animation {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.bg-grid {
  background-image: 
    linear-gradient(rgba(255, 255, 255, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255, 255, 255, 0.03) 1px, transparent 1px);
  background-size: 50px 50px;
}

.bg-gradient {
  background: radial-gradient(circle at 50% 50%, rgba(132, 71, 255, 0.15), transparent 70%),
              radial-gradient(circle at 70% 30%, rgba(212, 175, 55, 0.1), transparent 50%);
  filter: blur(60px);
}

/* Timeline */
.timeline {
  position: relative;
  margin: 2rem 0;
}

.timeline-item {
  position: relative;
  padding-left: 2rem;
  margin-bottom: 2rem;
  border-left: 2px solid var(--diamond-purple);
}

.timeline-item:last-child {
  margin-bottom: 0;
}

.timeline-date {
  font-weight: bold;
  color: var(--diamond-pink);
  margin-bottom: 0.5rem;
}

.timeline-title {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
}

.timeline-content {
  color: var(--text-secondary);
}

/* Responsividade */
@media (max-width: 992px) {
  h1 {
    font-size: 2.5rem;
  }
  
  h2 {
    font-size: 2rem;
  }
  
  section {
    padding: 4rem 0;
  }
  
  .desktop-menu {
    display: none;
  }
}

@media (min-width: 992px) {
  .desktop-menu {
    display: flex;
  }
}

@media (max-width: 768px) {
  h1 {
    font-size: 2rem;
  }
  
  h2 {
    font-size: 1.8rem;
  }
  
  section {
    padding: 3rem 0;
  }
  
  .card {
    padding: 1.5rem;
  }
}

@media (max-width: 576px) {
  h1 {
    font-size: 1.8rem;
  }
  
  h2 {
    font-size: 1.5rem;
  }
  
  .container {
    padding: 0 1rem;
  }
  
  section {
    padding: 2.5rem 0;
  }
}

/* Animações */
.nav-link {
  position: relative;
}

.nav-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(to right, var(--diamond-purple), var(--tp-gold));
  transition: width 0.3s ease;
}

.nav-link:hover::after {
  width: 100%;
}

/* Estilos co-branded */
.co-branded-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.co-branded-logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.co-branded-divider {
  height: 40px;
  width: 2px;
  background: linear-gradient(to bottom, var(--diamond-purple), var(--tp-gold));
  margin: 0 1rem;
}

/* Melhorias para mobile */
@media (max-width: 576px) {
  .co-branded-logo {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .co-branded-divider {
    height: 2px;
    width: 40px;
    margin: 0.5rem 0;
  }
  
  .card {
    padding: 1.25rem;
  }
  
  .btn {
    width: 100%;
    margin-bottom: 0.5rem;
  }
}
